@import url("https://fonts.googleapis.com/css?family=Quicksand&display=swap");
@import url("https://fonts.googleapis.com/css?family=Montserrat&display=swap");

/* For titles use font-family: "Montserrat", sans-serif; 
for text use font-family: "Quicksand", sans-serif*/

.app {
  display: flex;
  flex-direction: column;
  padding: 0px;
  margin: 0px;
  background-color: #bebb89;
}

.app-mylogo {
  height: 50px;
  width: 50px;
}

.fa-html5 {
  color: #e46713;
}

.fa-css3-alt {
  color: #2873e4;
}

.fa-js-square {
  color: #ddd64d;
}

.fa-bootstrap {
  color: #6f3ac5;
}

.fa-sass {
  color: #d188cd;
}

.fa-react {
  color: #44c5ec;
}

.fa-python {
  color: #4b76ee;
}

.fa-node-js {
  color: #64b422;
}

.fa-npm {
  color: #d64214;
}

.fa-tensorflow,
.fa-d3 {
  max-width: 50px;
  pointer-events: none;
}

.tf-div {
  user-select: none;
}

.sections img {
  width: 100%;
}

.sections > section {
  min-height: 80vh;
  padding: 40px 0px 40px 0px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  text-align: center;
}

.sections > section h1,
h2,
h3,
h4,
h5,
h6 {
  font-family: "Montserrat", sans-serif;
}

.sections > section p {
  font-family: "Quicksand", sans-serif;
  text-align: center;
  margin: auto;
  padding: 10px;
}

.sections .img-flex {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: center;
}

.sections .img-flex > div {
  width: 20vw;
  min-width: 200px;
  align-self: center;
  padding: 20px;
}

.section-text {
  max-width: 80vw;
}

.intro-section {
  background-image: url("./images/JCLogo3.svg");
  background-repeat: no-repeat;
  background-position: center;
  margin-top: 50px;
}

.skills-array {
  padding: 20px;
}
.skills-array div {
  padding: 10px;
}

#contact-section {
  padding: 0px;
  min-height: 10vh;
  display: flex;
  flex-direction: row;
  justify-content: space-evenly;
}

.fa-linkedin-in {
  color: #eef1f7;
}

.university-logo {
  position: relative;
  top: 50%;
  transform: translateY(-50%);
  max-height: 80px;
  padding: 10px;
}

.grad-date {
  position: relative;
  top: 50%;
  transform: translateY(-50%);
}

.education-row {
  padding-top: 20px;
}

.contact-email {
  display: flex;
  flex-direction: row;
  font-family: "Quicksand", sans-serif;
  align-items: center;
}
.contact-email > span {
  display: flex;
  flex-direction: row;
  align-items: center;
}

.contact-email > span > p {
  margin: auto;
}
